@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;800&display=swap");
:root {
  /*     Common Colors     */
  --common-color: #00a0e9;
  /*     Font Size        */
  --fs-900: 9.375rem;
  --fs-800: 6.25rem;
  --fs-700: 3.5rem;
  --fs-600: 2rem;
  --fs-500: 1.75rem;
  --fs-400: 1.125rem;
  --fs-300: 1rem;
  --fs-200: 0.875rem;
  --fs-100: 0.65rem;
  /*     Font Color       */
  --clr-white: 0 0% 100%;
  --mica-tint: 0,0%,13%;
  --mica-tint-opacity: 0.8;
  --control-strong-color-default: hsla(0,0%,100%,.545);
}

.theme--light {
  --background: white;
  --foreground: black;
  --clr-accent: 230 35% 7%;
  --clr-hover: 230 35% 7%;
  --clr-dark: 231 77% 90%;
  --clr-light: 0 0% 0%;
}

.theme--dark {
  --background: #111111;
  --foreground: white;
  --clr-accent: 231 77% 90%;
  --clr-hover: 230 35% 7%;
  --clr-dark: 230 35% 7%;
  --clr-light: 360 100% 100%;
  --clr-bg: #119191;
}

/*      Font Utility Size Class     */
.fs-900 {
  font-size: var(--fs-900);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-200 {
  font-size: var(--fs-200);
}

.fs-100 {
  font-size: var(--fs-100);
}

/*      Font Utility Color Class    */
.text-accent {
  color: hsl(var(--clr-accent)/0.7);
}

.text-hover {
  color: hsl(var(--clr-accent));
}

body {
  font-family: "Inter", sans-serif;
}

/* ------------------- */
/* Reset               */
/* ------------------- */
/* Box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reset margins */
body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 400;
}

/* set up the body */
body {
  font-size: var(--fs-400);
  color: hsl(var(--clr-white));
  background-color: hsl(var(--clr-dark));
  line-height: 1.5;
  min-height: 100vh;
}

/* make images easier to work with */
img,
picutre {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flow-gap {
  margin-top: var(--flow-space, 1rem) !important;
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
     supported by Chrome, Edge, Opera and Firefox */
}

.uppercase {
  text-transform: uppercase;
}

.tagline {
  font-weight: 800;
  font-size: calc(2rem + 1vw);
  background: var(--bg-gradient, linear-gradient(to right, #00c6fb 0%, #005bea 100%));
  background-clip: text;
  background-position: left;
  background-size: 200%;
  color: transparent;
  letter-spacing: 0.01rem;
  word-spacing: 0.001rem;
  text-transform: uppercase;
  transition: 1s;
}

a, button {
  transition: all 200ms ease-in-out;
}

.tagline:hover {
  background-clip: text;
  background-position: right;
  background-size: 200%;
}

body {
  background: var(--background);
  color: var(--foreground);
  font-size: 1rem;
}
body .master-container {
  min-height: 80vh;
}
body .master-container .master-container__inner {
  display: grid;
