.scroller::-webkit-scrollbar:hover {
  background: var(--scrollbar-caret-bottom) bottom center/contain no-repeat, var(--scrollbar-caret-top) top center/contain no-repeat, hsl(var(--mica-tint), var(--mica-tint-opacity));
}
