.master-container__inner .event-container .event__gallery .event__gallery-wrapper .gallery__item .gallery__item-content .merge-sides::before, .master-container__inner .event-container .event__gallery .event__gallery-wrapper .gallery__item .gallery__item-content .merge-sides::after {
